.movie {
    position: fixed;
    top: 50%;
    left: 50%;
    height: 800px;
    transform: translate(-50%, -50%);
    background-color: white;
    margin-bottom: 70px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    display: grid;
    grid-template-columns: minmax(150px, 1fr) 2fr;
    grid-gap: 20px;
    text-decoration: none;
    color: inherit;
    /* background-image: url(movies.background_image); */
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3),
        0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie__img {
    position: relative;
    top: -50px;
    width: 300px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0, 0, 0, 0.3),
        0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie__background {
    position: relative;
    top: 100px;
    width: 100%;
}
.movie__title,
.movie__year {
    margin-bottom: auto;
    font-weight: 600;
    text-decoration: none;
}

/* .movie__title a {
    margin-bottom: 5px;
