    color: #2c2c2c;
    text-decoration: none;
} */

.movie__runtime {
    margin-bottom: 5px;
    font-size: 24px;
}
.movie__summary {
    list-style: none;
    font-size: 16px;
    font-weight: 300;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0px;
    margin-top: 20px;
    margin-bottom: 10px;
}
.movie__genres {
    list-style: none;
    padding: 0;
    margin: 10;
    bottom: 0;
    display: flex;
    flex-wrap: wrap;
    position: absolute;
}

.movie__genres li {
    margin: 20px 15px 0px 00px;
    font-weight: 500;
    font-size: 14px;
}

.movie__year {
    margin-right: 15px;
    font-size: 12px;
}
